import React from "react"
import { useStaticQuery, graphql, Link} from "gatsby"

import styled, { withTheme } from "styled-components"
import Img from "gatsby-image"

const AboutContainer = styled.div`
  display: grid;
  grid-template-columns: auto;
  z-index: 10;
  background-color: ${(props) => props.theme.landingTextBackground};
  margin: 0 10vw;
  padding: 2vh 0;
  box-shadow: ${(props) => props.theme.landingTextBoxShadow};
  border-radius: 2px;
  & > * {
    padding: 1vh 3vw;
  }
  @media screen and (min-width: 600px) {
    padding:2vh 2vw;
    grid-template-columns: repeat(2, 1fr);
  }
`;
const ContentContainer = styled.div`
  @media screen and (min-width:600px) {
    text-align:left;
  }
  text-align:center;
  div a {
    display: inline-block;
    margin: 1vh 1vw;
    text-decoration: none;
    height: 35px;
    @media screen and (min-width: 600px) {
      height: 40px;
    }

    transition: color .15s ease 0s, border .15s ease .1s;
    border-bottom: 1px solid ${(props) => props.theme.borderColor};
    }
  }
  div a:hover {
    border-color: ${props => props.theme.linksHover};
    transition: color .15s ease 0s, border .15s ease .1s;
  }
`;
const ImageContainer = styled.div`
  align-self: center;
  .gatsby-image-wrapper {
    border-radius: 8px;
    width: 75%;
    margin: 0 auto;
  }
  p {
    text-align: center;
  }
  .subline {
    width: 75%;
    margin: 0 auto;
    margin-top: 2vh;
    font-size: 20px;
    letter-spacing: 1px;
    font-style: italic;
  }
`;
const About = () => {
  const data = useStaticQuery(graphql`
    {
      file(relativePath: { eq: "profile-picture.jpg" }) {
        childImageSharp {
          fluid {
            aspectRatio
            base64
            sizes
            src
            srcSet
          }
        }
      }
    }
  `);

  return (
    <div className="container">
      <AboutContainer>
        <ContentContainer>
          <h3>Tacky Tagline</h3>
          <p>I'm a software-developer building full-stack web applications for businesses. My goal is to help clients take their platforms to the web, ultimately making the internet a more functional space.</p>
          <div>
            <a target="_blank" rel="noopener noreferrer" href="https://github.com/raleigh9123">
              <p>GitHub</p>
            </a>
            {/* <a target="_blank" href="https://www.linkedin.com/in/joseph-butterfield-853aaa90/">
              <p>LinkedIn</p>
            </a> */}
            <a target="_blank" rel="noopener noreferrer" href="https://twitter.com/jo2tony">
              <p>Twitter</p>
            </a>
            <Link to="/about"><p>About Me</p></Link>
          </div>
        </ContentContainer>
        <ImageContainer>
          <Img
            fluid={data.file.childImageSharp.fluid}
            alt="Joseph Butterfield Profile"
            style={{ "object-fit": "cover" }}
          />
          <p className="subline">Make the internet a more functional space</p>
        </ImageContainer>
      </AboutContainer>
    </div>
  );
}

export default withTheme(About);
